import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

const api = "https://movie-database-imdb-alternative.p.rapidapi.com/";
const header = {
  "x-rapidapi-key": "9cb0dff204mshf7da16d827262f3p1ce205jsnc5f7587ebbda",
  "x-rapidapi-host": "movie-database-imdb-alternative.p.rapidapi.com",
};

const SET_ID = "SET_ID";
const SET_INDX = "SET_INDX";
const SET_INFO = "SET_INFO";
const SET_SEARCH = "SET_SEARCH";
const SET_MOVIES = "SET_MOVIES";
const SET_DIALOG = "SET_DIALOG";

const moduleInfo = {
  state: { id: {}, indx: {}, info: [], search: {}, movies: [], dialog: false },
  getters: {
    id: (state) => {
      return state.id;
    },
    indx: (state) => {
      return state.indx;
    },
    info: (state) => {
      return state.info;
    },
    search: (state) => {
      return state.search;
    },
    movies: (state) => {
      return state.movies;
    },
    dialog: (state) => {
      return state.dialog;
    },
  },
  mutations: {
    [SET_ID](state, id) {
      state.id = id;
    },
    [SET_INDX](state, indx) {
      state.indx = indx;
    },
    [SET_INFO](state, info) {
      state.info = info;
    },
    [SET_SEARCH](state, search) {
      state.search = search;
    },
    [SET_MOVIES](state, movies) {
      state.movies = movies;
    },
    [SET_DIALOG](state, dialog) {
      state.dialog = dialog;
    },
  },
  actions: {
    async listMovies({ commit, state }) {
      const options = {
        method: "GET",
        url: api,
        params: { s: state.search, page: "1", r: "json" },
        headers: header,
      };
      await axios
        .request(options)
        .then((response) => (this.movies = response.data.Search))
        .catch((error) => console.log(error));
      commit(SET_MOVIES, this.movies);
    },

    async viewInfo({ commit, state }) {
      const options = {
        method: "GET",
        url: api,
        params: { i: state.id, r: "json" },
        headers: header,
      };

      await axios
        .request(options)
        .then((response) => (this.info = response.data))
        .catch((error) => console.log(error));
      commit(SET_INFO, this.info);
    },
  },
};

const store = new Vuex.Store({
  modules: {
    modinfo: moduleInfo,
  },
});

export default store;
